import {Box, Content, Heading} from 'react-bulma-components';
import {useTranslation} from "react-i18next";

/**
 * Patent List
 */
const PatentList = () => {

    const {t} = useTranslation();

    return (
        <Box id="patent">
            <Heading size={5}>
                {t("patent")}
            </Heading>
            <Content>
                <ul>
                    <li>
                        <strong></strong>, 2023
                    </li>
                    <li>
                        <strong>5G</strong>, CN107426774A, 2017
                    </li>
                </ul>
            </Content>

        </Box>
    )
};

export default PatentList;
